import React, { Component, Fragment } from "react";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdHistory,
  MdSearch,
} from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../../actions";
import { FC_GetPositionsByInstitution } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { PositionAPI, SessionPlanInterface } from "../../../interfaces";
import {
  MarksPublicationCandidateStatus,
  marksPublicationReportContentInterface,
  marksPublicationCandidateReportContentKeyInterface,
  MarksPublicationCandidateToPrintInterface,
  MarksPublicationCandidate,
} from "../../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import {
  generatePDF,
  reportSettingsInterface,
} from "../../../components/DownloadPDF/rest";
import { BiExport, BiSort } from "react-icons/bi";
import {
  MainItem,
  SideNavigation,
} from "../../../components/Navigation/SideNavigation";
import { FiEdit } from "react-icons/fi";
import TableLoading from "../../../components/Loading/TableLoading";
import { ImFilesEmpty } from "react-icons/im";
import { errorToText, search, sortThisArray } from "../../../utils/functions";
import Loading from "../../../components/Loading/Loading";
import { GetAllSessionPlans } from "../../../actions/session";
import axios from "axios";
import { API } from "../../../utils/api";
import ChooseSession from "../../SessionSettings/ChooseSession";
import LoadingV2, { LoadingType } from "../../../components/Loading/LoadingV2";
import { Link } from "react-router-dom";
import { IoFilter } from "react-icons/io5";
import { IoIosBriefcase } from "react-icons/io";
import ExportToExcel from "../../../utils/ExportToExcel";
import NoSession from "../../SessionSettings/NoSession";

const defaultReportContent: marksPublicationReportContentInterface[] = [
  {
    key: marksPublicationCandidateReportContentKeyInterface.applicant_name,
    title: "Applicant name",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.email,
    title: "Email",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.phone_number,
    title: "Phone",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.gender,
    title: "Gender",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.position_name,
    title: "Position",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.marks,
    title: "Marks",
    status: true,
  },
  {
    key: marksPublicationCandidateReportContentKeyInterface.status,
    title: "Status",
    status: true,
  },
];

interface MarksPublicationReportProps {
  //   REDUX ############################
  auth: Auth;
  systemPositions: PositionAPI[] | null;
  application_sessions: SessionPlanInterface[] | null;

  GetAllSessionPlans: Function;
  getSchools: Function;
  FC_GetPositionsByInstitution: Function;
  getAllRoom: Function;
  selectDistrict: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface MarksPublicationReportState {
  loading: boolean;
  loadingData: boolean;
  selectAll: boolean;
  reportMainData: MarksPublicationCandidate[] | null;
  reportContentExport: marksPublicationReportContentInterface[];
  reportTitle: string;
  dataToPrint: MarksPublicationCandidateToPrintInterface[] | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  searchPositionInput: string;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectedStatus: MarksPublicationCandidateStatus | "all";
  selectPosition: boolean;
  selectedPosition: PositionAPI | null;
  selectedSession: SessionPlanInterface | null;
  selectSession: boolean;
  downloading: boolean;
}

export class _MarksPublicationReport extends Component<
  MarksPublicationReportProps,
  MarksPublicationReportState
> {
  constructor(props: MarksPublicationReportProps) {
    super(props);

    this.state = {
      loading: true,
      loadingData: true,
      selectedStatus: "all",
      selectAll: true,
      // selectedReportTitle: centerReportTitleType;
      reportMainData: null,
      reportContentExport: defaultReportContent,
      reportTitle: "",
      dataToPrint: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      searchPositionInput: "",
      generateReportModal: null,
      formError: null,
      selectPosition: false,
      selectedPosition: null,
      selectedSession: null,
      selectSession: false,
      downloading: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setLoadingData = (status: boolean) => {
    this.setState({ loadingData: status });
  };

  setSelectAll = (status: boolean) => {
    this.setState({ selectAll: status });
  };

  setSelectSession = (status: boolean) => {
    this.setState({ selectSession: status });
  };

  setSelectedSession = (thisSession: SessionPlanInterface) => {
    this.setState({
      selectedSession: thisSession,
      reportTitle:
        "Application marks publication Report - " +
        thisSession!.session_plan_name,
    });
  };

  setSelectPosition = (status: boolean) => {
    this.setState({ selectPosition: status });
  };

  setSelectedPosition = (thisPosition: PositionAPI | null) => {
    this.setState({ selectedPosition: thisPosition });
  };

  setReportTitle = (thisTitle: string) => {
    this.setState({ reportTitle: thisTitle });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setGenerateReportModal = (
    data: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: data });
  };

  setDownloading = (value: boolean) => {
    this.setState({ downloading: value });
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  loadReportMainData = async (thisSession: SessionPlanInterface) => {
    console.log("=================loadReportMainData===================");
    console.log({ thisSession });
    console.log("=================loadReportMainData===================");
    this.setLoadingData(true);
    try {
      // const res = await axios.get<MarksPublicationCandidate[]>(
      //   `${API.DO_EXAM}/waiting/publication/${thisSession._id}`
      // );
      const res = await axios.get<MarksPublicationCandidate[]>(
        `${API.DO_EXAM}/waiting/publication/${thisSession._id}`
      );
      console.log("====================================");
      console.log({ reportMainData: res });
      console.log("====================================");
      this.setState({ reportMainData: res.data });
    } catch (error) {
      console.error({ reportMainData_error: error });
      // callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.setLoadingData(false);
  };

  componentDidMount = () => {
    if (
      this.props.systemPositions === null &&
      this.props.auth.education_institution_id
    ) {
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          console.log({ status: status, msg: msg });
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (this.state.loading) {
      this.setLoading(false);
    }

    if (
      this.props.application_sessions === null ||
      this.props.application_sessions.length === 0
    ) {
      this.props.GetAllSessionPlans(
        (
          status: boolean,
          errorMsg: string,
          sessionData: SessionPlanInterface[]
        ) => {
          this.setLoading(status);
          if (!status) {
            if (!isEmptyOrSpaces(errorMsg)) {
              this.props.PushPopupAlert({
                title: errorMsg,
                theme: PopupAlertTheme.warning,
              });
            }

            if (sessionData && sessionData.length > 0) {
              let activeSession = sessionData.find((item) => item.is_active);
              if (activeSession) {
                this.setSelectedSession(activeSession);
                if (this.state.reportMainData === null) {
                  this.loadReportMainData(activeSession);
                }
              } else {
                this.setSelectSession(true);
              }
            }
          }
        }
      );
    } else {
      this.setLoading(false);
      if (this.props.application_sessions.length > 0) {
        let activeSession = this.props.application_sessions.find(
          (item) => item.is_active
        );
        if (activeSession) {
          this.setSelectedSession(activeSession);
          if (this.state.reportMainData === null) {
            this.loadReportMainData(activeSession);
          }
        } else {
          this.setSelectSession(true);
        }
      }
    }
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (this.state.selectedSession) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
        title: "Marks publication Report",
        subjectTitle: `Application marks publication`,
        subject: "All position",
        period: this.state.selectedSession.session_plan_name,
        footer: "Online Recruitment",
        institute: "All institute",
        orientation: "l",
      };
      table && generatePDF(reportSettings, table, this.setDownloading);
    }
  };

  updateContentToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (this.state.selectedSession) {
      let title =
        "Application marks publication Report - " +
        this.state.selectedSession.session_plan_name;
      this.setReportTitle(title);
    }
    // UPDATE CONTENT =============================
    if (this.state.reportMainData) {
      let arrayNew: MarksPublicationCandidateToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.state.reportMainData) {
        if (
          Object.prototype.hasOwnProperty.call(this.state.reportMainData, key)
        ) {
          let thisPosition = this.props.systemPositions
            ? this.props.systemPositions.find(
                (item) =>
                  item.position_id === this.state.reportMainData![key].position
              )
            : undefined;
          let dataArray: MarksPublicationCandidateToPrintInterface | null =
            null;
          dataArray = {
            no: n,
            names:
              this.state.reportMainData![key].fname +
              " " +
              this.state.reportMainData![key].lname,
            national_id: this.state.reportMainData![key].n_id,
            phone: this.state.reportMainData![key].phone_number,
            email: this.state.reportMainData![key].email,
            gender: this.state.reportMainData![key].gender,
            position_name: thisPosition ? thisPosition.position_title : "",
            marks: this.state.reportMainData![key].user_position_marks,
            status: this.state.reportMainData![key].waiting_status,
          };

          for (const key in this.state.reportContentExport) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContentExport,
                key
              )
            ) {
              if (!this.state.reportContentExport[key].status && dataArray) {
                delete dataArray[this.state.reportContentExport[key].key];
              }
            }
          }
          dataArray && arrayNew.push(dataArray);
          n++;
        }
      }
      this.setState({ dataToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  updateReportContent = (
    key: marksPublicationCandidateReportContentKeyInterface
  ) => {
    let index: number = this.state.reportContentExport.findIndex(
      (item) => item.key === key
    );
    let newArray: marksPublicationReportContentInterface[] =
      this.state.reportContentExport;
    newArray[index].status = !newArray[index].status;
    this.setState({ reportContentExport: newArray });
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateContentToPrint();
  };

  isChangeAllowed = (
    key: marksPublicationCandidateReportContentKeyInterface
  ): boolean => {
    if (
      key ===
        marksPublicationCandidateReportContentKeyInterface.applicant_name ||
      key ===
        marksPublicationCandidateReportContentKeyInterface.position_name ||
      key === marksPublicationCandidateReportContentKeyInterface.national_id
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: marksPublicationReportContentInterface[] =
      this.state.reportContentExport;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setState({ reportContentExport: newArray });
    this.setSelectAll(!currentState);
    this.updateContentToPrint();
  };

  dataToShow = (
    allData: MarksPublicationCandidate[]
  ): MarksPublicationCandidateToPrintInterface[] => {
    let result: MarksPublicationCandidateToPrintInterface[] = [];
    for (const key in allData) {
      if (Object.prototype.hasOwnProperty.call(allData, key)) {
        let thisPosition = this.props.systemPositions
          ? this.props.systemPositions.find(
              (item) => item.position_id === allData[key].position
            )
          : undefined;
        result.push({
          no: Math.random(),
          names: allData![key].fname + " " + allData![key].lname,
          national_id: allData![key].n_id,
          phone: allData![key].phone_number,
          email: allData![key].email,
          gender: allData![key].gender,
          position_name: thisPosition ? thisPosition.position_title : "",
          marks: Number(allData![key].user_position_marks.toFixed(2)),
          status: allData![key].waiting_status,
        });
      }
    }
    return result;
  };

  FC_FilterReportData = (
    allData: MarksPublicationCandidateToPrintInterface[],
    status: MarksPublicationCandidateStatus | "all",
    position: PositionAPI | null
  ): MarksPublicationCandidateToPrintInterface[] => {
    let result: MarksPublicationCandidateToPrintInterface[] = allData;
    if (status !== "all") {
      result = result.filter((item) => item.status === status);
    }

    if (position) {
      result = result.filter(
        (item) =>
          item.position_name.toLocaleLowerCase() ===
          position.position_title.toLocaleLowerCase()
      );
    }

    return result;
  };

  ReportStatuses = (
    allData: MarksPublicationCandidate[]
  ): {
    title: string;
    count: number;
    status: MarksPublicationCandidateStatus | "all";
  }[] => {
    let output: {
      title: string;
      count: number;
      status: MarksPublicationCandidateStatus | "all";
    }[] = [{ title: "All", count: allData.length, status: "all" }];
    output.push(
      {
        title: capitalizeFirstLetter(
          MarksPublicationCandidateStatus.Passed.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.waiting_status === MarksPublicationCandidateStatus.Passed
        ).length,
        status: MarksPublicationCandidateStatus.Passed,
      },
      {
        title: capitalizeFirstLetter(
          MarksPublicationCandidateStatus.Failed.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.waiting_status === MarksPublicationCandidateStatus.Failed
        ).length,
        status: MarksPublicationCandidateStatus.Failed,
      }
    );
    return output;
  };

  render() {
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    if (
      this.props.application_sessions &&
      this.props.application_sessions.length === 0
    ) {
      return <NoSession />;
    }

    return (
      <div>
        <SideNavigation>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Session:</div>
            <div
              onClick={() => this.setSelectSession(true)}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {this.state.selectedSession !== null
                ? `${this.state.selectedSession.session_plan_name}`
                : "Filter by session"}
              <FiEdit className="text-lg text-blue-600" />
            </div>
          </div>

          <div className="pt-1">
            <div className="text-sm text-gray-700 pb-2 pt-2">
              Performance statuses:
            </div>
            {this.state.reportMainData === null || this.state.loadingData ? (
              <Loading />
            ) : (
              this.ReportStatuses(this.state.reportMainData!).map(
                (
                  itm: {
                    title: string;
                    count: number;
                    status: MarksPublicationCandidateStatus | "all";
                  },
                  i: number
                ) => (
                  // itm.count > 0 &&
                  <Fragment key={itm.title}>
                    <div
                      onClick={() =>
                        this.setState({ selectedStatus: itm.status })
                      }
                    >
                      <MainItem
                        title={itm.title}
                        count={itm.count}
                        active={
                          this.state.selectedStatus === itm.status
                            ? true
                            : false
                        }
                      />
                    </div>
                  </Fragment>
                )
              )
            )}
          </div>
        </SideNavigation>

        {/* BODY CONTENT--------------------------------- */}
        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Applications marks publication Report
                </span>
                <div
                  onClick={() => this.setState({ selectPosition: true })}
                  className="flex flex-row gap-2 items-end"
                >
                  <span className="text-gray-500 text-xl">Position:</span>
                  <span className="text-blue-600 font-bold underline cursor-pointer text-xl">
                    {this.state.selectedPosition !== null
                      ? this.state.selectedPosition.position_title
                      : "All position"}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.state.reportMainData &&
                  this.state.reportMainData.length > 0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        disabled={
                          this.state.reportMainData && !this.state.downloading
                            ? false
                            : true
                        }
                        onClick={() => {
                          this.updateContentToPrint();
                          this.setGenerateReportModal({
                            reportType: "EXCEL",
                            status: true,
                          });
                        }}
                      >
                        EXCEL
                      </button>
                      <button
                        className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                          this.state.downloading && "cursor-wait"
                        }`}
                        onClick={() => this.FC_GeneratePDF()}
                        disabled={this.state.reportMainData ? false : true}
                      >
                        PDF
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          {/* ######################################################## */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.loadingData === null ||
                  this.state.reportMainData === null ||
                  this.FC_FilterReportData(
                    this.dataToShow(this.state.reportMainData),
                    this.state.selectedStatus,
                    this.state.selectedPosition
                  ).length <= 0
                }
              />
            </div>
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("names")}
                  >
                    <div className="flex items-center gap-1">
                      Names <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("national_id")}
                  >
                    <div className="flex items-center gap-1">
                      National Id <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("phone")}
                  >
                    <div className="flex items-center gap-1">
                      Phone <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("email")}
                  >
                    <div className="flex items-center gap-1">
                      Email <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("gender")}
                  >
                    <div className="flex items-center gap-1">
                      Gender <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("position_name")}
                  >
                    <div className="flex items-center gap-1">
                      Position <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("marks")}
                  >
                    <div className="flex items-center gap-1">
                      Marks <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("status")}
                  >
                    <div className="flex items-center gap-1">
                      Status <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.state.reportMainData === null ||
                this.state.loadingData ? (
                  <TableLoading cols={9} />
                ) : this.FC_FilterReportData(
                    this.dataToShow(this.state.reportMainData),
                    this.state.selectedStatus,
                    this.state.selectedPosition
                  ).length === 0 ? (
                  <tr>
                    <td colSpan={9} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found! Please try to search again
                        </span>
                        <div className="w-full mt-2 flex flex-row items-center justify-center">
                          <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                            <li
                              onClick={() =>
                                this.setState({ selectSession: true })
                              }
                              className="underline text-gray-500 cursor-pointer"
                            >
                              Choose another district
                            </li>
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                ) : (
                  sortThisArray(
                    this.state.isSorted,
                    search(
                      this.FC_FilterReportData(
                        this.dataToShow(this.state.reportMainData),
                        this.state.selectedStatus,
                        this.state.selectedPosition
                      ),
                      this.state.searchInput
                    ),
                    this.state.sortProperty,
                    this.state.sortOrder
                  ).map(
                    (item: MarksPublicationCandidateToPrintInterface, i) => (
                      <tr
                        key={i + item.no}
                        className={`hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-sm ${
                          item.status === MarksPublicationCandidateStatus.Failed
                            ? "border-yellow-500 text-gray-700 bg-yellow-200"
                            : "bg-white"
                        }`}
                      >
                        <td className="px-2 py-2">{i + 1}</td>
                        <td className="px-2 py-2">{item.names}</td>
                        <td className="px-2 py-2">{item.national_id}</td>
                        <td className="px-2 py-2">{item.phone}</td>
                        <td className="px-2 py-2">{item.email}</td>
                        <td className="px-2 py-2">{item.gender}</td>
                        <td className="px-2 py-2">{item.position_name}</td>
                        <td className="px-2 py-2">{item.marks}</td>
                        <td className="px-2 py-2">{item.status}</td>
                      </tr>
                    )
                  )
                )}
              </tbody>
            </table>
          </div>
        </section>
        {/* END BODY CONTENT--------------------------------- */}

        {/* ---------------------------------------------------------------------------- */}

        {/* SELECT SESSION MODAL =========================== */}
        {this.state.selectSession && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <MdHistory className="text-5xl text-blue-600" />
                <span className="flex flex-col">Choose session</span>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.state.selectedSession !== null &&
              this.setSelectSession(false)
            }
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <ChooseSession
              allSession={this.props.application_sessions}
              setSelectSession={(value: SessionPlanInterface | "all") => {
                if (value !== "all") {
                  this.setSelectedSession(value);
                  this.loadReportMainData(value);
                  this.setSelectSession(false);
                }
              }}
              chooseAll={false}
              currentSelected={
                this.state.selectedSession ? this.state.selectedSession : "all"
              }
            />
          </Modal>
        )}

        {/* ---------------------------------------------------------------------------- */}

        {/* POSITION CONTENT MODAL */}
        {this.state.selectPosition === true && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <IoFilter className="text-5xl text-blue-600" />
                <div className="flex flex-col">
                  <span className="font-bold text-xl text-blue-600">
                    Filter by a position
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selectPosition: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-2 pb-2">
              {this.state.selectPosition === true &&
              this.props.systemPositions === null ? (
                <LoadingV2 type={LoadingType.dark} />
              ) : this.props.systemPositions!.length === 0 ? (
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No positions found! Please go back!</span>
                    <Link
                      to="/"
                      className="btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200"
                    >
                      Go back
                    </Link>
                  </div>
                </div>
              ) : (
                <>
                  <div className="flex flex-col">
                    <span className="text-gray-600 text-xs font-bold">
                      Filter by position name
                    </span>
                    <input
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        this.setState({ searchPositionInput: e.target.value })
                      }
                      type="text"
                      className="mb-4 bg-gray-100 px-3 py-2 rounded"
                      placeholder="Keyword..."
                    />
                  </div>
                  <div
                    onClick={() => {
                      this.setSelectedPosition(null);
                      this.setState({
                        selectPosition: false,
                        reportTitle:
                          "Application marks publication Report - " +
                          this.state.selectedSession
                            ? this.state.selectedSession!.session_plan_name
                            : "",
                      });
                    }}
                    className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                  >
                    <div className="grid lg:grid-cols-12">
                      <div className="col-span-12 lg:col-span-10">
                        <div className="flex flex-row items-center gap-3">
                          <div>
                            <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                          </div>
                          <div className="flex flex-col">
                            <span className="text-xs lg:text-base font-bold text-gray-700">
                              All positions
                            </span>
                          </div>
                        </div>
                      </div>
                      <div className="col-span-12 lg:col-span-2 hidden lg:block">
                        <div className="h-full flex items-center justify-end">
                          <div className="btn bg-blue-600 text-white float-right">
                            Choose
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {search(
                    this.props.systemPositions!,
                    this.state.searchPositionInput
                  ).map((item: PositionAPI, key: number) => (
                    <div
                      key={key + 1}
                      onClick={() => {
                        this.setSelectedPosition(item);
                        this.setState({
                          selectPosition: false,
                          reportTitle:
                            "Application marks publication Report - " +
                            this.state.selectedSession
                              ? this.state.selectedSession!.session_plan_name +
                                " on " +
                                item.position_title
                              : "",
                        });
                      }}
                      className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                    >
                      <div className="grid lg:grid-cols-12">
                        <div className="col-span-12 lg:col-span-10">
                          <div className="flex flex-row items-center gap-3">
                            <div>
                              <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                            </div>
                            <div className="flex flex-col">
                              <span className="text-xs lg:text-sm font-bold text-gray-700">
                                {item.position_title}
                              </span>
                              <span className="text-xs text-gray-500 italic">
                                School level:{" "}
                                <span
                                  className="text-gray-800 font-light"
                                  style={{ fontStyle: "normal" }}
                                >
                                  {item.school_level_id.school_level_name}
                                </span>
                              </span>
                              <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">
                                {
                                  item.education_institute_id
                                    .education_category_title
                                }
                              </span>
                            </div>
                          </div>
                        </div>
                        <div className="col-span-12 lg:col-span-2 hidden lg:block">
                          <div className="h-full flex items-center justify-end">
                            <div className="btn bg-blue-600 text-white float-right">
                              Choose
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </>
              )}
            </div>
          </Modal>
        )}
        {/* END POSITION CONTENT MODAL */}

        {/* GENERATE REPORT MODAL CONTENT MODAL */}
        {this.state.generateReportModal !== null &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setGenerateReportModal(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setFormError(null);
                        this.setReportTitle(e.target.value);
                      }}
                      disabled={this.state.reportMainData ? false : true}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContentExport.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.dataToPrint && this.state.reportMainData && (
                    <ExportToExcel
                      fileData={this.state.dataToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
        {/* END GENERATE REPORT MODAL CONTENT MODAL */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    systemPositions: system_settings.systemPositions,
    application_sessions: sessionPlan.allSessionPlans,
  };
};

export const MarksPublicationReport = connect(mapStateToProps, {
  GetAllSessionPlans,
  FC_GetPositionsByInstitution,
  PushPopupAlert,
})(_MarksPublicationReport);
